a,
abbr,
acronym,
address,
applet,
article,
aside,
audio,
b,
big,
blockquote,
body,
canvas,
caption,
center,
cite,
code,
dd,
del,
details,
dfn,
div,
dl,
dt,
em,
embed,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
i,
iframe,
img,
ins,
kbd,
label,
legend,
li,
mark,
menu,
nav,
object,
ol,
output,
p,
pre,
q,
ruby,
s,
samp,
section,
small,
span,
strike,
strong,
sub,
summary,
sup,
table,
tbody,
td,
tfoot,
th,
thead,
time,
tr,
tt,
u,
ul,
var,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}
body {
	line-height: 1;
}
ol,
ul {
	list-style: none;
}
blockquote,
q {
	quotes: none;
}
blockquote:after,
blockquote:before,
q:after,
q:before {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-decoration,
input[type='search']::-webkit-search-results-button,
input[type='search']::-webkit-search-results-decoration {
	-webkit-appearance: none;
	-moz-appearance: none;
}
input[type='search'] {
	-webkit-appearance: none;
	-moz-appearance: none;
	-webkit-box-sizing: content-box;
	-moz-box-sizing: content-box;
	box-sizing: content-box;
}
textarea {
	overflow: auto;
	vertical-align: top;
	resize: vertical;
}
audio,
canvas,
video {
	display: inline-block;
	max-width: 100%;
}
audio:not([controls]) {
	display: none;
	height: 0;
}
[hidden] {
	display: none;
}
html {
	font-size: 62.5%;
	-webkit-text-size-adjust: 62.5%;
	-ms-text-size-adjust: 62.5%;
}
a:focus {
	outline: thin dotted;
}
a:active,
a:hover {
	outline: 0;
}
img {
	border: 0;
	-ms-interpolation-mode: bicubic;
}
figure {
	margin: 0;
}
form {
	margin: 0;
}
fieldset {
	border: 1px solid silver;
	margin: 0 2px;
	padding: 0.35em 0.625em 0.75em;
}
legend {
	border: 0;
	padding: 0;
	white-space: normal;
}
button,
input,
select,
textarea {
	font-size: 100%;
	margin: 0;
	vertical-align: baseline;
}
button,
input {
	line-height: normal;
}
button,
select {
	text-transform: none;
}
button,
html input[type='button'],
input[type='reset'],
input[type='submit'] {
	-webkit-appearance: button;
	cursor: pointer;
}
button[disabled],
html input[disabled] {
	cursor: default;
}
input[type='checkbox'],
input[type='radio'] {
	box-sizing: border-box;
	padding: 0;
}
input[type='search'] {
	-webkit-appearance: textfield;
	-moz-box-sizing: content-box;
	-webkit-box-sizing: content-box;
	box-sizing: content-box;
}
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-decoration {
	-webkit-appearance: none;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
	border: 0;
	padding: 0;
}
textarea {
	overflow: auto;
	vertical-align: top;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
button,
html,
input,
select,
textarea {
	color: #222;
}
::-moz-selection {
	background: #b3d4fc;
	text-shadow: none;
}
::selection {
	background: #b3d4fc;
	text-shadow: none;
}
img {
	vertical-align: middle;
}
fieldset {
	border: 0;
	margin: 0;
	padding: 0;
}
textarea {
	resize: vertical;
}
.chromeframe {
	margin: 0.2em 0;
	background: #ccc;
	color: #000;
	padding: 0.2em 0;
}

.clearfix:after {
	visibility: hidden;
	display: block;
	font-size: 0;
	content: '';
	clear: both;
	height: 0;
}

* {
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

.visuallyHidden:not(:focus):not(:active) {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(00 0 0);
	overflow: hidden;
}
.wrapper {
	max-width: 1280px;
	margin: 0 auto;
	width: 85%;
}
.app{
	display:flex;
	flex-wrap: wrap;
}
header{
	width: 100%;
}
h1 {
	font-size: 6rem;
	padding: 2rem;
	text-align: center;
}
h2 {
	font-size: 3rem;
	text-align: center;
	text-transform: capitalize;
	padding: 4rem 0;
}
.btn {
	display: inline-block;
	border: none;
	background: white;
	color: black;
	padding: 1rem 2rem;
	text-decoration: none;
	font-size: 1.5rem;
	letter-spacing: 0.7px;
	text-transform: capitalize;
	border: 3px solid black;
	border-radius: 1rem;
	/* margin:0 2rem; */
}

.btn:hover {
	border: 3px solid gray;
	}
.btn:focus {
	border: 3px solid gray;
	}
.main{
	width: 50%;
}
form {
  padding: 3rem 0;
  font-size: 1.5rem;
  display:flex;
  flex-wrap: wrap;
  width:50%;
  
}

.inputClass label{
  padding:2rem;
  font-size: 2rem;
  font-weight: bold;
  width: 40%;
  text-align: right;

}
input[type='text'] {
  margin:1rem 0;
  padding: 10px;
  width: 40%;
  /* min-width: 40px; */
  
}
.select{
	display: inline-block;
	margin-right: 1rem;
}

button[type='submit'] {
 font-size: 3rem;

}
@media only screen and (max-width: 900px){
	.wrapper{
		width:90%
	}
	form {
		width:60%;
		
	  }
	  h1 {
		font-size: 5rem;
	}
	
}
@media only screen and (max-width: 700px){
	form {
		width:80%;
		
	  }
	  h1 {
		font-size: 5rem;
	}
	.inputClass label{
		
		font-size: 1.5rem;
	  }
	  .btn {
		padding: 5px;
		
	}

	
}
@media only screen and (max-width: 530px){
	form {
		width:100%;
		
	  }
	  h1 {
		font-size: 4rem;
	}
}

